"use client";

import React from "react";
import { WeatherData } from "@/lib/weather";
import { Cloud, CloudRain, Sun } from "lucide-react";

interface ForecastProps {
    data: WeatherData;
}

export default function Forecast({ data }: ForecastProps) {
    const { daily } = data;

    // Helper to get day name
    const getDayName = (dateStr: string) => {
        const date = new Date(dateStr);
        return date.toLocaleDateString("en-US", { weekday: "short" });
    };

    const renderIcon = (code: number) => {
        if (code <= 1) return <Sun size={20} className="text-yellow-500" style={{ color: "#fbbf24" }} />;
        if (code <= 3) return <Cloud size={20} className="text-gray-400" style={{ color: "#9ca3af" }} />;
        return <CloudRain size={20} className="text-blue-400" style={{ color: "#60a5fa" }} />;
    };

    return (
        <div className="card" style={{ padding: "1.5rem" }}>
            <h3 style={{ marginBottom: "1rem", fontSize: "1.2rem" }}>7-Day Forecast</h3>
            <div style={{ display: "flex", flexDirection: "column", gap: "1rem" }}>
                {daily.time.map((time, index) => (
                    <div
                        key={time}
                        style={{
                            display: "flex",
                            alignItems: "center",
                            justifyContent: "space-between",
                            paddingBottom: index !== daily.time.length - 1 ? "0.5rem" : 0,
                            borderBottom: index !== daily.time.length - 1 ? "1px solid var(--border)" : "none",
                        }}
                    >
                        <span style={{ width: "50px", fontWeight: "bold" }}>
                            {index === 0 ? "Today" : getDayName(time)}
                        </span>
                        <div style={{ display: "flex", alignItems: "center", gap: "0.5rem" }}>
                            {renderIcon(daily.weatherCode[index])}
                            <span style={{ opacity: 0.7, fontSize: "0.9rem" }}>
                                {daily.weatherCode[index] <= 1 ? "Sunny" : daily.weatherCode[index] <= 3 ? "Cloudy" : "Rainy"}
                            </span>
                        </div>
                        <div style={{ display: "flex", gap: "1rem" }}>
                            <span style={{ fontWeight: "bold" }}>{Math.round(daily.temperatureMax[index])}°</span>
                            <span style={{ opacity: 0.6 }}>{Math.round(daily.temperatureMin[index])}°</span>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
}
