"use client";

import React from "react";
import { WeatherData } from "@/lib/weather";
import { Droplets, Wind, Compass, Sun } from "lucide-react";

interface WeatherDetailsProps {
    data: WeatherData;
}

export default function WeatherDetails({ data }: WeatherDetailsProps) {
    const { current } = data;

    const details = [
        {
            icon: <Droplets size={24} />,
            label: "Humidity",
            value: `${current.humidity}%`,
        },
        {
            icon: <Wind size={24} />,
            label: "Wind Speed",
            value: `${current.windSpeed} km/h`,
        },
        {
            icon: <Compass size={24} />,
            label: "Wind Direction",
            value: `${current.windDirection}°`,
        },
        {
            icon: <Sun size={24} />,
            label: "UV Index",
            value: "N/A", // Open-Meteo free tier basic doesn't always have UV easily without extra params, keeping simple
        },
    ];

    return (
        <div className="grid" style={{ gridTemplateColumns: "repeat(auto-fit, minmax(150px, 1fr))", gap: "1rem" }}>
            {details.map((item, index) => (
                <div key={index} className="card" style={{ display: "flex", flexDirection: "column", alignItems: "center", justifyContent: "center", padding: "1.5rem" }}>
                    <div style={{ marginBottom: "0.5rem", color: "var(--primary)" }}>{item.icon}</div>
                    <p style={{ fontSize: "0.9rem", opacity: 0.7 }}>{item.label}</p>
                    <p style={{ fontSize: "1.2rem", fontWeight: "bold" }}>{item.value}</p>
                </div>
            ))}
        </div>
    );
}
