"use client";

import React, { useEffect, useState } from "react";
import SearchBar from "@/components/SearchBar";
import CurrentWeather from "@/components/CurrentWeather";
import WeatherDetails from "@/components/WeatherDetails";
import Forecast from "@/components/Forecast";
import ThemeToggle from "@/components/ThemeToggle";
import { getWeather, WeatherData, City } from "@/lib/weather";

export default function Home() {
  const [data, setData] = useState<WeatherData | null>(null);
  const [city, setCity] = useState<string>("London");
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);

  const fetchWeather = async (lat: number, lon: number, cityName: string) => {
    setLoading(true);
    setError(null);
    try {
      const weather = await getWeather(lat, lon);
      if (weather) {
        setData(weather);
        setCity(cityName);
      } else {
        setError("Failed to fetch weather data.");
      }
    } catch (err) {
      setError("An error occurred.");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    // Default to London
    fetchWeather(51.5074, -0.1278, "London");
  }, []);

  const handleCitySelect = (selectedCity: City) => {
    fetchWeather(selectedCity.latitude, selectedCity.longitude, selectedCity.name);
  };

  return (
    <main className="container" style={{ minHeight: "100vh", paddingBottom: "2rem" }}>
      <header style={{ display: "flex", justifyContent: "space-between", alignItems: "center", marginBottom: "2rem" }}>
        <h1 style={{ fontSize: "1.5rem", fontWeight: "bold" }}>Weather App</h1>
        <ThemeToggle />
      </header>

      <div style={{ marginBottom: "2rem" }}>
        <SearchBar onCitySelect={handleCitySelect} />
      </div>

      {loading ? (
        <div className="card text-center">
          <p>Loading weather data...</p>
        </div>
      ) : error ? (
        <div className="card text-center" style={{ color: "red" }}>
          <p>{error}</p>
        </div>
      ) : data ? (
        <div className="grid-layout">
          <div className="main-section">
            <CurrentWeather data={data} city={city} />
            <div className="mt-4">
              <WeatherDetails data={data} />
            </div>
          </div>
          <div className="forecast-section">
            <Forecast data={data} />
          </div>
        </div>
      ) : null}

      <style jsx>{`
        .grid-layout {
          display: grid;
          grid-template-columns: 1fr;
          gap: 1.5rem;
        }

        @media (min-width: 768px) {
          .grid-layout {
            grid-template-columns: 2fr 1fr;
          }
        }
      `}</style>
    </main>
  );
}
