"use client";

import React, { useState, useEffect, useRef } from "react";
import { Search, MapPin } from "lucide-react";
import { searchCity, City } from "@/lib/weather";

interface SearchBarProps {
    onCitySelect: (city: City) => void;
}

export default function SearchBar({ onCitySelect }: SearchBarProps) {
    const [query, setQuery] = useState("");
    const [results, setResults] = useState<City[]>([]);
    const [isLoading, setIsLoading] = useState(false);
    const [isOpen, setIsOpen] = useState(false);
    const wrapperRef = useRef<HTMLDivElement>(null);

    useEffect(() => {
        const handleClickOutside = (event: MouseEvent) => {
            if (wrapperRef.current && !wrapperRef.current.contains(event.target as Node)) {
                setIsOpen(false);
            }
        };
        document.addEventListener("mousedown", handleClickOutside);
        return () => document.removeEventListener("mousedown", handleClickOutside);
    }, []);

    useEffect(() => {
        const delayDebounceFn = setTimeout(async () => {
            if (query.length >= 2) {
                setIsLoading(true);
                const cities = await searchCity(query);
                setResults(cities);
                setIsLoading(false);
                setIsOpen(true);
            } else {
                setResults([]);
                setIsOpen(false);
            }
        }, 500);

        return () => clearTimeout(delayDebounceFn);
    }, [query]);

    const handleSelect = (city: City) => {
        onCitySelect(city);
        setQuery("");
        setResults([]);
        setIsOpen(false);
    };

    return (
        <div ref={wrapperRef} className="search-container" style={{ position: "relative", width: "100%", maxWidth: "500px", margin: "0 auto" }}>
            <div className="search-input-wrapper" style={{ display: "flex", alignItems: "center", position: "relative" }}>
                <Search size={20} style={{ position: "absolute", left: "12px", color: "var(--foreground)", opacity: 0.5 }} />
                <input
                    type="text"
                    value={query}
                    onChange={(e) => setQuery(e.target.value)}
                    placeholder="Search for a city..."
                    style={{
                        width: "100%",
                        padding: "12px 12px 12px 40px",
                        borderRadius: "24px",
                        border: "1px solid var(--border)",
                        backgroundColor: "var(--card)",
                        color: "var(--foreground)",
                        fontSize: "1rem",
                        outline: "none",
                        boxShadow: "var(--shadow)",
                    }}
                    onFocus={() => query.length >= 2 && setIsOpen(true)}
                />
            </div>

            {isOpen && (results.length > 0 || isLoading) && (
                <ul
                    className="search-results"
                    style={{
                        position: "absolute",
                        top: "100%",
                        left: 0,
                        right: 0,
                        marginTop: "8px",
                        backgroundColor: "var(--card)",
                        border: "1px solid var(--border)",
                        borderRadius: "12px",
                        boxShadow: "var(--shadow)",
                        listStyle: "none",
                        padding: "8px 0",
                        zIndex: 50,
                        maxHeight: "300px",
                        overflowY: "auto",
                    }}
                >
                    {isLoading ? (
                        <li style={{ padding: "12px 16px", color: "var(--foreground)", opacity: 0.7 }}>Loading...</li>
                    ) : (
                        results.map((city) => (
                            <li
                                key={city.id}
                                onClick={() => handleSelect(city)}
                                style={{
                                    padding: "12px 16px",
                                    cursor: "pointer",
                                    display: "flex",
                                    alignItems: "center",
                                    gap: "8px",
                                    transition: "background-color 0.2s",
                                }}
                                onMouseEnter={(e) => (e.currentTarget.style.backgroundColor = "var(--input)")}
                                onMouseLeave={(e) => (e.currentTarget.style.backgroundColor = "transparent")}
                            >
                                <MapPin size={16} style={{ opacity: 0.5 }} />
                                <span>
                                    {city.name}, {city.admin1 ? `${city.admin1}, ` : ""}
                                    {city.country}
                                </span>
                            </li>
                        ))
                    )}
                </ul>
            )}
        </div>
    );
}
