"use client";

import React from "react";
import { WeatherData, getWeatherIcon } from "@/lib/weather";
import { Cloud, CloudRain, Sun, Wind } from "lucide-react";

interface CurrentWeatherProps {
    data: WeatherData;
    city: string;
}

export default function CurrentWeather({ data, city }: CurrentWeatherProps) {
    const { current, daily } = data;
    const temp = Math.round(current.temperature);
    const high = Math.round(daily.temperatureMax[0]);
    const low = Math.round(daily.temperatureMin[0]);

    // Simple icon mapping logic for now
    // In a real app, I'd map weatherCode to specific icons more robustly
    const renderIcon = () => {
        const code = current.weatherCode;
        if (code <= 1) return <Sun size={64} className="text-yellow-500" style={{ color: "#fbbf24" }} />;
        if (code <= 3) return <Cloud size={64} className="text-gray-400" style={{ color: "#9ca3af" }} />;
        if (code >= 50) return <CloudRain size={64} className="text-blue-400" style={{ color: "#60a5fa" }} />;
        return <Sun size={64} />;
    };

    return (
        <div className="card" style={{ textAlign: "center", padding: "2rem" }}>
            <h2 style={{ fontSize: "2rem", marginBottom: "0.5rem" }}>{city}</h2>
            <div style={{ display: "flex", justifyContent: "center", margin: "1rem 0" }}>
                {renderIcon()}
            </div>
            <div style={{ fontSize: "4rem", fontWeight: "bold", lineHeight: 1 }}>
                {temp}°
            </div>
            <p style={{ fontSize: "1.2rem", color: "var(--foreground)", opacity: 0.8, marginTop: "0.5rem" }}>
                H: {high}° L: {low}°
            </p>
        </div>
    );
}
